import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withContentRect } from 'react-measure';
import isFunction from 'lodash/isFunction'

import Box from './Box';

class VerticalCenter extends PureComponent {
  static getDerivedStateFromProps({ contentRect }, { containerRef, containerHeight }) {
    if (containerRef && contentRect.bounds.height) {
      const newContainerHeight = containerRef.getBoundingClientRect().height
      if (newContainerHeight && contentRect.bounds.height) {
        const shouldCenter = newContainerHeight > containerHeight
          ? newContainerHeight >= contentRect.bounds.height
          : newContainerHeight > contentRect.bounds.height + 1;
        return {
          shouldCenter,
          containerHeight: newContainerHeight,
        }
      }
    }
    return {}
  }

  state = {}

  componentDidMount() {
    this.props.measure()
  }

  componentDidUpdate(prevProps, prevState) {
    const { shouldCenter } = this.state
    if (shouldCenter !== prevState.shouldCenter) {
      this.props.onAlignChange(shouldCenter)
    }
  }

  handleContainerRef = (containerRef) => {
    this.setState({ containerRef })
  }

  render() {
    const {
      children,
      onAlignChange,
      measure,
      measureRef,
      contentRect,
      ...props
    } = this.props;
    const { shouldCenter } = this.state;
    return (
      <Box
        position="relative"
        height="100%"
        ref={this.handleContainerRef}
        {...props}
      >
        <Box
          position={shouldCenter && 'absolute'}
          top={shouldCenter ? '50%' : 0}
          width={1}
          transform={shouldCenter && 'translateY(-50%)'}
          ref={measureRef}
        >
          {isFunction(children) ? children(shouldCenter) : children}
        </Box>
      </Box>
    );
  }
}

VerticalCenter.displayName = 'VerticalCenter';

VerticalCenter.propTypes = {
  onAlignChange: PropTypes.func,
}

VerticalCenter.defaultProps = {
  onAlignChange: () => {}
}

export default withContentRect('bounds')(VerticalCenter);
