import React from 'react';
import Box from './Box'
import Flex from './Flex'
import Text from './Text'
import Image from './Image'
import {mobileOrDesktop} from './ThemeProvider/theme'

const Title = ({children, icon, title, description, fontWeight, width, fontSize, desfontSize, imgwidth, textAlign, ...props}) => {
  return (
    <Flex alignItems="center" px="2em" {...props}>
      <Box width={mobileOrDesktop(width ? width : 1/3, width ? width : 1/11)} p="1em">
        <Image width={imgwidth} src={icon} />
      </Box>
      <Box width={mobileOrDesktop(3/4, 'auto')} py={mobileOrDesktop('0.5em', '1em')} textAlign={textAlign ? textAlign : 'left'} mx={mobileOrDesktop('0.25em', '1em')}>
        <Text.h2 fontSize={mobileOrDesktop('1.25em', fontSize ? fontSize : '1.5em')} fontWeight={fontWeight ? fontWeight : 'bold'}>{title}</Text.h2>
        <Text fontSize={mobileOrDesktop('0.83em', desfontSize ? desfontSize :'1em')} whiteSpace="pre-wrap">{description}</Text>
      </Box>
    </Flex>
  );
};

export default Title;
